/* Footer.css */
.footer {
  background-color: #141a29;
  /* Adjust color as needed */
  color: #f3f3f3;
  padding: 20px 0;
  text-align: center;
  font-family: Arial, sans-serif;
}

.footer-container {
  max-width: 1200px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.footer-logo h2 {
  font-size: 1.8em;
  font-weight: 600;
  margin: 0;
  color: #ffffff;
}

.footer-links a {
  margin: 0 15px;
  color: #f3f3f3;
  text-decoration: none;
  font-size: 1em;
}

.footer-links a:hover {
  color: #aaa;
}

.footer-social a {
  color: #f3f3f3;
  font-size: 1.2em;
  margin: 0 10px;
  transition: color 0.3s;
}

.footer-social a:hover {
  color: #aaa;
}

.footer-bottom {
  border-top: 1px solid #444;
  padding-top: 10px;
  font-size: 0.9em;
  color: #aaa;
}